@font-face {
    font-family: 'fontAwesome';
    src:    url('../fonts/icons/fontAwesome.eot?j4zpuy');
    src:    url('../fonts/icons/fontAwesome.eot?j4zpuy#iefix') format('embedded-opentype'),
    url('../fonts/icons/fontAwesome.ttf?j4zpuy') format('truetype'),
    url('../fonts/icons/fontAwesome.woff?j4zpuy') format('woff'),
    url('../fonts/icons/fontAwesome.svg?j4zpuy#fontAwesome') format('svg');
    font-weight: normal;
    font-style: normal;
}

.icon, [class^="icon-"], [class*=" icon-"]  {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'fontAwesome' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

